import React from 'react';
import { Search, Heart, Package, Camera } from 'lucide-react';

/**
 * Empty state component for various scenarios
 */
export const EmptyState = ({ 
  type = 'products', 
  title, 
  description, 
  action, 
  onAction 
}) => {
  const configs = {
    products: {
      icon: <Search className="w-16 h-16" />,
      title: title || 'Nincs tallat',
      description: description || 'Prblj meg ms keressi kifejezst hasznlni, vagy bngszd a kategrikat.',
      iconColor: 'text-indigo-600',
      bgColor: 'bg-indigo-50'
    },
    wishlist: {
      icon: <Heart className="w-16 h-16" />,
      title: title || 'res a kvnsglistd',
      description: description || 'Mg nem mentettl el kedvenc termkeket. Kezdj el bngszni!',
      iconColor: 'text-red-500',
      bgColor: 'bg-red-50'
    },
    category: {
      icon: <Package className="w-16 h-16" />,
      title: title || 'Ebben a kategriban nincsenek termkek',
      description: description || 'Hamarosan j termkekkel bvlnk. Nzz vissza ksbb!',
      iconColor: 'text-gray-600',
      bgColor: 'bg-gray-100'
    },
    upload: {
      icon: <Camera className="w-16 h-16" />,
      title: title || 'Tlts fel egy kpet',
      description: description || 'Hasznld az AI kpkerest hogy megtalld a hasonl termkeket.',
      iconColor: 'text-purple-600',
      bgColor: 'bg-purple-50'
    }
  };

  const config = configs[type] || configs.products;

  return (
    <div className="flex flex-col items-center justify-center py-20 px-4 text-center">
      <div className={`${config.bgColor} ${config.iconColor} rounded-full p-6 mb-6`}>
        {config.icon}
      </div>
      <h3 className="text-2xl font-bold text-gray-900 mb-3">{config.title}</h3>
      <p className="text-gray-500 max-w-md mb-8">{config.description}</p>
      {action && onAction && (
        <button 
          onClick={onAction}
          className="bg-indigo-600 text-white px-6 py-3 rounded-xl font-bold hover:bg-indigo-700 transition-all transform hover:-translate-y-0.5 shadow-lg hover:shadow-xl"
        >
          {action}
        </button>
      )}
    </div>
  );
};
